<template>
  <div class="createActive">
    <mHeader  sidebar="ture"  :head-title="$t('appoint.createEvent')" ref="child1"></mHeader>
    <goTop/>
    <div class="createContent">
      <div class="content">
        <div class="createTitle">
          <em></em>
          <i></i>
          <span>{{$t('appoint.activePlan')}}</span>
          <i></i>
          <em></em>
        </div>
        <h5>{{token}}</h5>
        <div class="plan">
          <ul>
            <li>
              <a href="#" @click="radioShow = true">
              <input type="text" :value="selectVal" :placeholder="$t('appoint.themeSelect')" disabled>
              <img src="../../images/case/birthday/select.png" alt="">
            </a>
            </li>
            <li>
              <a href="#" @click="dateShow = true">
                <input type="text" :value="dateVal" :placeholder="$t('appoint.dateSelect')" disabled>
                <img src="../../images/case/birthday/select.png" alt="">
              </a>
            </li>
            <li>
              <input type="number" v-model="gestPersion" :placeholder="$t('appoint.peoples')">
              <!--<img src="../../images/case/birthday/select.png" alt="">-->
            </li>
            <li>
              <a href="#" @click="moneyShow=true">
                <input type="text" :value="moneyVal" :placeholder="$t('appoint.budget')" disabled>
                <img src="../../images/case/birthday/select.png" alt="">
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div class="content">
        <div class="createTitle">
          <em></em>
          <i></i>
          <span>{{$t('appoint.activeSite')}}</span>
          <i></i>
          <em></em>
        </div>
        <div class="textarea">
          <textarea name="" id="textarea" v-model="addressBus" :placeholder="$t('appoint.siteContent')"></textarea>
        </div>
        <div>

        </div>
      </div>
      <div class="content">
        <div class="createTitle">
          <em></em>
          <i></i>
          <span>{{$t('appoint.activeInfo')}}</span>
          <i></i>
          <em></em>
        </div>
        <div class="textarea">
          <textarea name="" id="textarea" v-model="informateBus"  :placeholder="$t('appoint.infoContent')"></textarea>
        </div>
      </div>
      <div class="content">
        <div class="createTitle">
          <em></em>
          <i></i>
          <span>{{$t('appoint.contacts')}}</span>
          <i></i>
          <em></em>
        </div>
        <div class="contact">
          <ul>
            <li>
              <div>
                <input type="text" v-model="contactName" :placeholder="$t('appoint.name')">
              </div>
              <div>
                <a @click="sexShow = true">
                  <input type="text" :value="sexVal" :placeholder="$t('appoint.sex')" disabled>
                  <img src="../../images/case/birthday/select.png" alt="">
                </a>
              </div>
            </li>
            <li>
              <input type="number" v-model="contactPhone" :placeholder="$t('appoint.phone')">
            </li>
            <li>
              <input type="text" v-model="contactWechat" :placeholder="$t('appoint.wechat')">
            </li>
            <li>
              <input type="text" v-model="contactEmail" :placeholder="$t('appoint.email')">
            </li>
          </ul>
          <h4 style="color: #d3c9b3">{{$t('appoint.safe')}}</h4>
        </div>
      </div>

      <div class="commit" @click="sidebar">
        <span>{{$t('appoint.submit')}}</span>
      </div>
    </div>

    <van-popup v-model="radioShow">
      <div class="createSelect radioSel">

        <van-radio-group v-model="radio" @change="clickHandler">
          <van-cell-group>
            <van-cell :title="$t('home.birthdayParty')" clickable @click="radio = '1'">
              <van-radio name="1" />
            </van-cell>

            <van-cell :title="$t('home.business')" clickable @click="radio = '2'">
              <van-radio name="2" />
            </van-cell>

            <van-cell :title="$t('home.familyFeast')" clickable @click="radio = '3'">
              <van-radio name="3" />
            </van-cell>

            <van-cell :title="$t('home.specificEvent')" clickable @click="radio = '4'">
              <van-radio name="4" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
        <!--<ul>
          <li style="background: #f0f0f0"><em>生日派对</em><img  src="../../images/case/selected.png" alt=""></li>
          <li><em>商务聚餐</em><img src="../../images/case/select.png" alt=""></li>
          <li><em>家庭聚会</em><img src="../../images/case/selected.png" alt=""></li>
          <li><em>私人定制</em><img src="../../images/case/select.png" alt=""></li>
          &lt;!&ndash;<li ref="selectBac" v-for='(item, index) in themeSelect' v-on:click='clickHandler(item,index)'><em>{{item}}</em>&lt;!&ndash;<img src="../../images/case/selected.png" alt="">&ndash;&gt;</li>&ndash;&gt;
        </ul>-->
      </div>
    </van-popup>
    <!--
    <van-datetime-picker
        v-model="currentDate"
        :min-date="minDate"
        :max-date="maxDate"
        :min-hour="minHour"
        :max-hour="maxHour"
        @confirm="dateConfirm"
        @cancel="dateCancel"
      />
    -->
    <van-popup class="wmd" v-model="dateShow" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        :min-date="minDate"
        @confirm="dateConfirm"
        @cancel="dateCancel"
        :confirm-button-text="$t('appoint.confirm')"
        :cancel-button-text="$t('appoint.cancel')"
      />
    </van-popup>
    <!--预算范围-->
    <van-popup v-model="moneyShow">
      <div class="createSelect moneySel">
        <van-radio-group v-model="radio" @change="clickMoney">
          <van-cell-group>
            <van-cell title="10000-29999" clickable @click="radio = '1'">
              <van-radio name="1" />
            </van-cell>

            <van-cell title="30000-49999" clickable @click="radio = '2'">
              <van-radio name="2" />
            </van-cell>

            <van-cell title="50000+" clickable @click="radio = '3'">
              <van-radio name="3" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
        <!--<ul>
          <li v-for='(item, index) in moneySelect' v-on:click='clickMoney(item,index)'><em>{{item}}</em></li>
        </ul>-->
      </div>
    </van-popup>
    <!--先生/女士-->
    <van-popup v-model="sexShow">
      <div class="createSelect sexSel">
        <van-radio-group v-model="radio" @change="changeRadio">
          <van-cell-group>
            <van-cell :title="$t('appoint.mr')" clickable @click="radio='1'">
              <van-radio name="1" />
            </van-cell>
            <van-cell :title="$t('appoint.ms')" clickable @click="radio='2'">
              <van-radio name="2" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
    </van-popup>
  </div>
</template>

<script>
  import mHeader from '../../components/header/headerTab.vue'
  import goTop from '../../components/goTop.vue';

  export default {
    created(){
    },
    data(){
      return {
        radioShow: false,
        dateShow: false,
        moneyShow: false,
        sexShow: false,
        radio: '',
        themeSelect:[this.$t('birthday.topTitle'),this.$t('business.topTitle'),this.$t('homeParty.topTitle'),this.$t('individuation.topTitle')],
        moneySelect:['10000-29999','30000-49999','50000+'],
        selectVal: '',
        moneyVal: '',
        dateVal: '',
        sexVal: '',
        minDate: new Date(),
        currentDate: new Date(),

        gestPersion:'',
        addressBus:'',
        informateBus:'',
        contactEmail:'',
        contactWechat:'',
        contactPhone: '',
        contactName:'',

        token:''
      }
    },
    mounted(){
      window.addEventListener('scroll', this.handleScroll)
    },

    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleScroll () {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      },
      async sidebar(){
        if (!this.selectVal) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.selectTheme')
          });
          return
        }else if (!this.dateVal) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.selectDate')
          });
          return
        }else if (!this.gestPersion) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputPeople')
          });
          return
        }else if (!this.moneyVal) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.selectRange')
          });
          return
        } else if (!this.addressBus) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputSite')
          });
          return
        } else if (!this.informateBus) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputInfo')
          });
          return
        }else if (!this.contactName) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputName')
          });
          return
        }else if (!this.sexVal) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.selectSex')
          });
          return
        }
        else if (!this.contactPhone) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputTel')
          });
          return
        } else if (!this.contactWechat) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputWechat')
          });
          return
        } else if (!this.contactEmail) {
          this.$toast({
            duration: 1500,
            forbidClick: true, // 禁用背景点击
            message: this.$t('other.inputEmail')
          });
          return
        }

        await this.$post('/feast_v1/order/reservation',{
          theme: this.selectVal,
          guests: this.gestPersion,
          budget_price: this.moneyVal,
          use_time: this.dateVal,
          address: this.addressBus,
          information: this.informateBus,
          contact_name:this.contactName,
          contact_phone:this.contactPhone,
          contact_wechat: this.contactWechat,
          contact_email: this.contactEmail,
          contact_gender: this.sexVal
        })
          .then((response) => {
          if(response.error_code==10000){
            this.$toast({
              duration: 1500,
              forbidClick: true, // 禁用背景点击
              message: response.message,
            });
          }else{
            this.$toast({
              duration: 1500,
              forbidClick: true, // 禁用背景点击
              message: response.message,
            });
          }
           this.$router.push('/mine')
            /*if(){
              // 返回值提示
            }else{
              // 跳转保存信息
              this.RECORD_USERINFO(this.userInfo);
              this.$router.go(-1);
            }*/
          })
      },
      /*clickHandler(item,index) {
        this.selectVal = item;
        this.radioShow = false;
        // index.style.background='red'
      },*/

      clickHandler(name) {
        if(name==1){
          this.selectVal = this.$t('birthday.topTitle');
        }else if(name==2){
          this.selectVal=this.$t('business.topTitle');
        }else if(name==3){
          this.selectVal = this.$t('homeParty.topTitle');
        }else if(name==4){
          this.selectVal = this.$t('individuation.topTitle');
        }

        this.radioShow = false;
        // index.style.background='red'
      },

      dateConfirm(value){
          let y = value.getFullYear();
          let m = value.getMonth() + 1;
          m = m < 10 ? ('0' + m) : m;
          let d = value.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = value.getHours();
          h = h < 10 ? ('0' + h) : h;
          let minute = value.getMinutes();
          minute = minute < 10 ? ('0' + minute) : minute;
          let date =  y + '-' + m + '-' + d + ' ' + h + ':' + minute;

        this.dateVal = date;
        this.dateShow=false
      },
      dateCancel(){
        this.dateShow=false
      },
      /*changeDate(value){
        console.log(value)
      },*/
      clickMoney(name){
        this.moneyShow = false;
        if(name==1){
          this.moneyVal = '10000-29999';
          this.moneyShow = false;
        }else if(name==2){
          this.moneyVal='30000-49999';
          this.moneyShow = false;
        }else if(name==3){
          this.moneyVal = '50000+';
          this.moneyShow = false;
        }
      },
     /* clickMoney(item){
        this.moneyVal = item;
        this.moneyShow = false;
      },*/
      changeRadio(name){
        this.sexVal = name==1?this.$t('appoint.mr'):this.$t('appoint.ms');
        this.sexShow = false;
      },
      sexname(){
        alert(1)
      }
    },
    components:{
      mHeader,
      goTop
    }

  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .createActive{

    input[disabled],input:disabled,input.disabled {
      color: #707070;
      -webkit-text-fill-color: #707070;
      -webkit-opacity: 1;
      opacity: 1;
    }

    input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {

      color:    #707070;
    }
    input:-moz-placeholder, textarea:-moz-placeholder {
      color:    #707070;
    }
    input::-moz-placeholder, textarea::-moz-placeholder {
      color:    #707070;
    }
    input:-ms-input-placeholder, textarea:-ms-input-placeholder {
      color:    #707070;
    }

      background: #1e1d17;
    .createContent{
      padding: .88rem .66rem .8rem .66rem;
      .content{
        width: 100%;
        .createTitle{
          padding-top: .7rem;
          padding-bottom: .1rem;
          @include fjc;
          em{
            height: 1px;
            width: .56rem;
            background: #d3c9b3;
          }
          i{
            @include wh(.08rem, .08rem);
            border-radius: .08rem;
            background: #d3c9b3;
            margin-left: .08rem;
            margin-right: .08rem;
          }
          span{
            font-size: .4rem;
            color: #d3c9b3;
            padding-left: .07rem;
            padding-right: .07rem;
          }
        }
        .plan{
          ul{
            padding: .16rem 0;
            li{
              position: relative;
              margin: .3rem 0;
              height: .8rem;
              input{
                position: absolute;
                background: rgba(49,49,49,0.8);
                border-radius: .1rem;
                width: 100%;
                height: .8rem;
                @include sc(.26rem, #707070);
                text-indent: .2rem;
              }
              img{
                position: absolute;
                top: .32rem;
                right: .32rem;
                @include wh(.2rem, .16rem);
              }
            }
          }
        }
        .textarea{
          width: 100%;
          margin-top: .46rem;
          #textarea{
            border-radius: .1rem;
            background: rgba(49,49,49,0.8);
            width: 100%;
            min-height: 1.3rem;
            padding: .2rem;
            @include sc(.26rem, #707070);
          }
        }
        .contact{
          ul{
            padding: .16rem 0;
            li{
              position: relative;
              margin: .3rem 0;
              height: .8rem;
              input{
                position: absolute;
                background: rgba(49,49,49,0.8);
                border-radius: .1rem;
                width: 100%;
                height: .8rem;
                @include sc(.26rem, #707070);
                text-indent: .2rem;
              }
              img{
                position: absolute;
                top: .32rem;
                right: .32rem;
                @include wh(.2rem, .16rem);
              }
            }
            li:nth-child(1){
              @include fjb;
              div{
                width: 2.96rem;
                input{
                  width: 2.9rem;
                }
              }
            }
          }
        }
        h4{
          text-align: right;
          @include sc(.2rem, #fff);
          font-style: italic;
        }
      }
      .commit{
        @include fjc;
        @include wh(100%, .8rem);
        border: solid 2px #d4be8c;
        border-radius: .4rem;
        margin-top: .8rem;
        span{
          @include sc(.32rem, #d4be8c);
        }
      }
    }

    .createSelect{
      ul{
        // padding: .2rem .5rem;
        background: #fff;
        width: 4.8rem;
        li{
          @include fjsb;
          width: 4.4rem;
          @include sc(.3rem, #000);
          padding: 0 .5rem;
          margin: 0 .2rem;
          line-height: .9rem;
          em{
            font-style: normal;
          }
          img{
            @include wh(.4rem, .4rem);
          }
        }
        li:hover{
          background: #f2f2f2;
        }
      }
    }
    .moneySel{
      ul li:nth-child(2){
        border-top: solid 1px #f0f0f0;
        border-bottom: solid 1px #f0f0f0;
      }
      ul li:nth-child(3){
        border-bottom: solid 1px #f0f0f0;
      }
      width: 5rem;
    }
    .radioSel{
      ul li:nth-child(2){
        border-top: solid 1px #f0f0f0;
        border-bottom: solid 1px #f0f0f0;
      }
      width: 5rem;
    }
    .sexSel{
      width: 5rem;
    }

    .confirm-button-text{
    }
  }
</style>



